import {
    GET_USERS_FETCH_REQUESTED, 
    GET_USERS_FETCH_SUCCEEDED, 
    GET_USERS_FETCH_FAILED, 
} from '../constants/Users' 

    const initState = {
        list: [],
        message: null,
    }

    const users = (state = initState, action) => {
        switch(action.type) {
            case GET_USERS_FETCH_REQUESTED: 
                return {
                    ...state,
                }
            
            case GET_USERS_FETCH_SUCCEEDED: 
                return{
                    ...state,
                    list: action.payload,
                }
            
            case GET_USERS_FETCH_FAILED: 
                return {
                    ...state,
                    message: action.message
                }

            default :
                return state;
    }
}

export default users;