import { all, takeEvery, put, fork, call, takeLatest } from 'redux-saga/effects';
import {getUser} from '../../api/getUser'

import {
    GET_USERS_FETCH_REQUESTED, 
    GET_USERS_FETCH_SUCCEEDED, 
    GET_USERS_FETCH_FAILED, 
} from '../constants/Users';

export function* fetchUserSaga() {
    try {
        const response = yield call(getUser)
        yield put({type: GET_USERS_FETCH_SUCCEEDED, payload: response.data})
    } catch (error) {
        yield put({type: GET_USERS_FETCH_FAILED, payload: error})
    }
}

export function* watchFetchData() {
    yield takeEvery(GET_USERS_FETCH_REQUESTED, fetchUserSaga)
}

export default function* rootSaga() {
    yield all([
        fork(watchFetchData),
    ])
}